:root {
  /* Colour Palette */
  --orange: #ff6b35;
  --peach: #f7c59f;
  --light-yellow: #efefd0;
  --blue: #1a659e;
  --dark-blue: #004e89;

  /* Other stylings */
  --border-radius: 8px;
}

body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
      rgb(239, 239, 208) 0%,
      rgba(255, 255, 255, 0) 100%
    )
    0% 0% / 100% 248px no-repeat;
  background-repeat: no-repeat;
}

strong {
  font-weight: 700;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.meet-logo {
  transition: all 200ms ease-in 0s;
  letter-spacing: 8px;
}

.meet-logo:hover {
  transition: all 200ms ease-in 0s;
  letter-spacing: normal;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: var(--border-radius);
  outline: none;
  border: 1px solid #ccc;
  align-self: center;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  margin: auto;
  position: relative;
  width: 80%;
}

.suggestions {
  position: relative;
  width: 100%;
  top: 0px !important;
  left: 0px !important;
  /* position: absolute; */
  /* z-index: 2; */
  align-self: center;
  /* margin: 0; */
  /* top: 43px; */
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event-card {
  margin-bottom: 1em;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: var(--border-radius);
  position: relative;
  transition: all 200ms ease-in 0s;
}

.event-card:hover {
  transition: all 200ms ease-in 0s;
  transform: scale(1.01);
}

.numberOfEvents {
  margin: 1em auto;
  text-align: center;
  display: inline-flex;
}

.events-text {
  margin-right: 0.5em;
}

.num-events {
  width: 5em;
}

.events-text {
  font-size: 1.2em;
  /* margin: 1em 1em; */
}

.event-card .card-footer {
  display: flex;
  justify-content: end;
}

.description {
  margin: 1em 0;
  overflow-x: hidden;
}

.details-btn {
  display: inline;
  /* position: absolute; */
  right: 1em;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: var(--border-radius);
  background-color: var(--dark-blue);
  /* Transitions */
  -webkit-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  transition-duration: 0.5s;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  transition: transform 0.3s cubic-bezier(0.38, 0.41, 0.27, 1) 0s,
    -webkit-transform 0.3s cubic-bezier(0.38, 0.41, 0.27, 1) 0s;
}

.details-btn:hover {
  transition: all 50ms ease-in 0s;
  opacity: 0.8;
  /* -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05); */
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

/* .Alert {
  position: fixed;
  top: 20px;
} */

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

/* When the browser is 400px and below */
@media screen and (max-width: 724px) {
  .NumberOfEvents {
    margin: 1em auto;
    width: 80%;
  }
}
